
module.exports = ({ html, useState, DynamicRendering}) =>
{
  return () =>
  {
    const [numbers, setNumbers] = useState(new Array(1000));


    return html`
    <div>
      ${numbers.map((number, index) =>
        {
          return html`<${DynamicRendering}>
            <li>${index}</li>
          </${DynamicRendering }>`
        })}
    </div>`
  }
}
